:root {
  --nav-height: 60px;
  --blue: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
  font-weight: 500;
}

.header {
  align-items: center;
  display: flex;
}

.container {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  align-items: center;
  text-decoration: none;
  display: flex;
}

.header__logo-img {
  width: 40px;
  height: auto;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  font-size: 12px;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  height: var(--nav-height);
  text-transform: uppercase;
  color: #000;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover {
  color: var(--blue);
}

.nav__link.is-active {
  color: var(--blue);
  position: relative;
}

.nav__link.is-active:hover {
  color: #000;
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--blue);
  border-radius: 8px;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
